.container{
    
    display:flex;
    justify-content: center;
    
 
    
    margin-top:190px;
    margin-left: 0px;
    margin-right: 0px;
    margin-bottom: 150px;
    opacity: 0;
    transform: scale(85%);
    transition: 1s;
    font-size: 2rem;
    
 
  
 
 }
 
 .container--zoom{
     opacity:1;
     transform: scale(100%);
     transition: 1s;
 }
 
 .container____image {
     width: 550px;
    
     box-shadow: 0px 6px 8px 0px;
     border-radius: 10px;
     max-height: 500px;
     margin-left: 0px;
     margin-left: 30px;
    
    
    
   
 }
 
 .container____content {
     display: flex;
     text-align: start;
     font-weight: 400;
     font-size: large;
  
     max-width: 600px;
     flex-direction: column;
     margin-left: 0px;
     margin-right:00px;;
   
 
  
 
     padding: 20px;
   
   
   
 
 }
 
 .container____title{
     
     margin-bottom: 20px;;
     font-weight: 900;
     max-width: 1300px;
     font-size: 2rem;
     
     
   
 
 }
 
 
     
 
 
 
 
 .text4{
     max-width: 640px;;
    font-size: small;
     line-height: 25px;
 }
 
